/*-----------------------------------------------------------------------------------*/
/*  Typography
/*-----------------------------------------------------------------------------------*/

body {
	font: {
		family: $font-family-base;
		size: $font-size-base;
	}
	
	line-height: $line-height-base;
	color: color(black);
}

	@include sm {
		body {
			font-size: $font-size-base-sm;
		}
	}

	@include md {
		body {
			font-size: $font-size-base-md;
		}
	}

.title {
	font: {
		family: $font-family-title;
	}
	line-height: $line-height-title;
	color: color(title);

	&__intro{
		font-size: $font-size-title1;
	}

	&__big{
		text-transform: uppercase;
		padding-bottom: 0;
		@extend h1
	}

	&__2{
		@extend h2
	}
}

h1 { 
	font-size: $font-size-title1;
	margin-bottom: 35px;
	font-weight: bold;	
} 
	@include sm {
		h1 {font-size: $font-size-title1-sm}
	}

	@include md {
		h1 {font-size: $font-size-title1-md}
	}


h2 { font-size: $font-size-title2;
	padding-bottom: 35px;
	font-weight: bold;
	color: color(title); }

	@include sm {
		h2 {font-size: $font-size-title2-sm}
		
	}

	@include md {
		h2 {font-size: $font-size-title2-md}
	}

h3 { font-size: $font-size-title3;
	padding-bottom: 20px; }

	@include sm {
		h3 {font-size: $font-size-title3-sm}
		
	}

	@include md {
		h3 {font-size: $font-size-title3-md}
	}

a {
	color: color(button);
	text-decoration: none;
	
	&:hover{ color: $hover;
	font-weight: bold;
	cursor: url(assets/img/cursorhover.png), pointer; }
}

.bouton{
	text-transform: uppercase;
	color: color(white);
	letter-spacing: 3px;
	border: none;
	cursor: pointer;
	background-color: color(button);
	padding: 20px 20px;
	border-radius: 20px;
	margin-top: 30px;
	box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.5);
	-moz-box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.5);

	&:hover{ background-color: $hover;
	cursor:	url(assets/img/cursorhover.png), pointer;
	color: white; }
}

.chiffre{
	font: {
		size: $font-size-chiffre;
		weight: bold;
		family: $font-family-title;
	}
	color: color(title);
	line-height: $line-height-chiffre;
}
	@include sm {
		.chiffre {font-size: $font-size-chiffre-sm;
			line-height: $line-height-chiffre-sm}
		
	}

	@include md {
		.chiffre {font-size: $font-size-chiffre-md}
	}

.text {

	&__logo{
		text-transform: uppercase;
		font-weight: bold;
		text-align: center;
		width: 100px;
		line-height: 1.4;
	}

	&__big{
		font-size: $font-size-big;
	}

	&__small{
		font-size: $font-size-small;
	}

	&__header{
		color: color(black);
	}

	&__black{
		color: color(black);
	}

	&__white{
		color: color(white);
		text-align: center;
	}

	&__left{
		position: relative;
		left: 20%;
	}
}