.tuto {
	height:auto;
	overflow: hidden;
	top:0px;
	left:0px;
	width: 100%; 
	margin:0px auto; 
	position: relative;
	text-align:center;	
}

.tuto__el{ 
	width: 17%; 
	min-width: 200px;
	height: auto; 
	margin:2%;
	vertical-align:middle;
	display:inline-block;
	overflow: hidden; 
	position: relative;
	text-align: left;
}