/*-----------------------------------------------------------------------------------*/
/*  Config
/*-----------------------------------------------------------------------------------*/

html {
	//font-size: 62.5%;
}

// Colors

$colors: (
  black: #000000,
  white: #fff,
  title: #3e695a,
  green: #e2ed9d,
  button: #89c284,
  text: #000000,
  icon: #73c31d,
  footer: #5e5b5b,
  filtre: #4293ad,
);
$hover: darken(map-get($colors, "button"), 20%);

@function color($key) {
  @if not map-has-key($colors, $key) {
    @warn "Key `#{$key}` not found in $colors map.";
  }
  @return map-get($colors, $key);
}


// Type

$font-family-base: "Titillium Web", sans-serif;
$font-family-title: "Amaranth", sans-serif;

$font-size-base: 14px;
$font-size-big: 23px;
$font-size-small: 13px;
$font-size-chiffre: 50px;


$font-size-title1: 39px;
$font-size-title2: 23px;
$font-size-title3: 15px;

$line-height-base: 1.8;
$line-height-title: 1.8;
$line-height-chiffre: 1.1;

/* SM */
$font-size-base-sm: 16px;
$font-size-big-sm: 26px;
$font-size-small-sm: 14px;
$font-size-chiffre-sm: 60px;


$font-size-title1-sm: 44px;
$font-size-title2-sm: 26px;
$font-size-title3-sm: 16px;

$line-height-base-sm: 1.8;
$line-height-title-sm: 1.8;
$line-height-chiffre-sm: 1.4;

/* MD */

$font-size-base-md: 18px;
$font-size-big-md: 29px;
$font-size-small-md: 14px;
$font-size-chiffre-md: 60px;


$font-size-title1-md: 46px;
$font-size-title2-md: 29px;
$font-size-title3-md: 18px;

$line-height-base-md: 2;
$line-height-title-md: 2;
$line-height-chiffre-md: 0.1;


::selection { background: #73c31d; }


// Small tablets and large smartphones (landscape view)
$screen-sm-min: 576px;

// Small tablets (portrait view)
$screen-md-min: 768px;

// Tablets and small desktops
$screen-lg-min: 992px;

// Large tablets and desktops
$screen-xl-min: 1200px;

// Small devices
@mixin sm {
   @media (min-width: $screen-sm-min) {
       @content;
   }
}

// Medium devices
@mixin md {
   @media (min-width: $screen-md-min) {
       @content;
   }
}

// Large devices
@mixin lg {
   @media (min-width: $screen-lg-min) {
       @content;
   }
}

// Extra large devices
@mixin xl {
   @media (min-width: $screen-xl-min) {
       @content;
   }
}
// Custom devices
@mixin rwd($screen) {
   @media (min-width: $screen+'px') {
       @content;
   }
}