/*-----------------------------------------------------------------------------------*/
/*  Layout
/*-----------------------------------------------------------------------------------*/
@import url('https://fonts.googleapis.com/css?family=Titillium+Web:400,400i,700');
@import url('https://fonts.googleapis.com/css?family=Amaranth:400,400i,700');

body{
	cursor: url("assets/img/souris.png"), pointer;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
%clear-fix {
    &:after {
        content: '';
        display: table;
        clear: both; 
    }
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto; 
  @extend %clearfix;
}

@include sm{
	.container{
	width: 88%;}
}

@include md{
	.container{
	width: 85%;}
}

/* COLONNES */

.col{
	width: 60%;
	&__center{
		text-align: center;
	}

	&__button{
		margin-top: 75px;
	}

	&__auto{
		margin: auto;
		padding: auto;
	}
}

@include sm {
	.col__text--big{
		width: 60%;
		margin-left: auto;
	  	margin-right: auto;
	}
}

@include md{
	.col{
		width: 39%;

		&__right{
			float: right;
		}
	}
}
section{
	padding: 100px 0;
}

.display{
	display: none;
}

/* Fin colonnes */

/* NAV MENU */
.logo {
    display: inline-block;
    float: left;
    width: 100px;
    color: color(white);
    margin-top: 10px;
}
.nav {
	font-family: $font-family-title;
    float: right;
    width: calc(100% - 100px);
    white-space: nowrap;
    color: color(white);
    text-transform: uppercase;
    top: 0;
    @extend %clear-fix;
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
	}

	.encours{
	    color: color(icon);
	}
}
	
.nav-list {
    float: right;
    > li {
        display: inline-block;
    }
    li {
        position: relative;
    }
    a {
        display: block;
        padding: 25px 20px;
        color: color(black);
        border: 1px solid transparent;
        font-size: 18px;
        text-decoration: none;
    }
    a:hover{
    	color: color(white);
    	padding: 25px 18px;
    }

    a:hover:nth-child(2){
    	padding: 10px;
    }

    .sub-menu {
        position: absolute;
        background-color: color(footer);
        width: auto;
        right: 0;
        top: 100%;
        width: 210px;
        display: none;
        a {
            padding: 8px 15px;
            border-bottom: 1px solid rgba(#fff, 0.1);
        }
        .sub-menu {
            top: 0;
            right: 100%;
        }
    }
    li:hover {
        > .sub-menu {
            display: block;
        }
    }
}

/* Fin nav menu */

/* HEADER */
.demo-header{
	background: #4293ad;
	background: -moz-linear-gradient(top, #4293ad 0%, #347083 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top, #4293ad 0%,#347083 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, #4293ad 0%,#347083 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4293ad', endColorstr='#347083',GradientType=0 ); /* IE6-9 */
  	background-position: center center;
  	position: relative;
  	vertical-align: top;
  	transform-origin: center top;
    padding: 0 15px;
    @extend %clear-fix;

    .row{
    	margin-top: 130px;
    }
}


/* Fin header */

/* INTRODUCTION */
.intro section{
	padding-bottom: 0;
	position: relative;
}

.intro{
	background-color: color(green);
	position: relative;
	bottom: 100px;
	width: 90%;
	vertical-align: top;
	height: auto;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 0;
	margin-top: 30px;
}

@include sm{
	.intro{
		max-width: 850px;
		margin-left: auto;
		margin-right: auto;
		padding: 50px;
		position: relative;
		bottom: 100px;
	}
}
@include md{
