/* Toggle */
.switch {
  position: relative;
  display: inline-block;
  width: 186px;
  height: 34px;
}

.switch input {display:none;}

.slider {
  position: absolute;
  cursor: url(assets/img/cursorhover.png), pointer;;
  top: 0;
  width: 46px;
  height: 25px;
  left: 145px;
  top: 3px;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(23px);
  -ms-transform: translateX(23px);
  transform: translateX(23px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}


