/* 404 ERROR */
html, .body__404{
    height: 100%;
    padding: 0;
    margin: 0;
}
.body__404 {
    display: flex;
    align-items: center;
    justify-content: center;
    color: color(yellow);
    font-family: "Titillium Web";
    background-image: url(assets/img/actif.gif);
    background-position: center;
    background-size: cover;
    width: 90%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;

    &:hover{
    	background-image: url(assets/img/hover.gif);
	}

	.error {
	    text-align: center;
	    margin: 0 auto;
	    padding-top: 5%;	

	    a {
		    text-transform: uppercase;
		    color: #ffffff;
		    text-decoration: none;
			letter-spacing: 3px;
			border: none;
			background-color: #89c284;
			padding: 10px 20px;
			border-radius: 20px;
			position: relative;
			top: 30px;


			&:hover{ background-color: $hover;
			color: white; }
		}
	} 

	h1 {
	    margin: 0;
	    font-size: 50px;
	    letter-spacing: 1px;
	    color: #3e695a;
	    font-family: "Amaranth";
	}

	p{
	    font-size: 33px;
	}
}



